'use client';

import { useState } from 'react';
import Navbar from '../../components/Navbar';
import Footer from '../../components/Footer';
import CartDrawer from '../../components/CartDrawer';
import { useCart } from '../../context/CartContext';
import { AnimatePresence } from 'framer-motion';

export default function LayoutWrapper({ children }: { children: React.ReactNode }) {
  const { cartItems, isCartOpen, onOpenCart, onCloseCart, updateQuantity, removeItem, cartItemCount } = useCart();

  return (
    <div className="min-h-screen bg-mensch-offwhite font-sans text-mensch-charcoal selection:bg-mensch-charcoal selection:text-white">
      <Navbar
        onOpenCart={onOpenCart}
        cartItemCount={cartItemCount}
      />

      <AnimatePresence>
        {isCartOpen && (
          <CartDrawer
            isOpen={isCartOpen}
            onClose={onCloseCart}
            items={cartItems}
            onUpdateQuantity={updateQuantity}
            onRemoveItem={removeItem}
          />
        )}
      </AnimatePresence>

      <main>{children}</main>

      <Footer />
    </div>
  );
}

